<template>
    <div class="content">
        <div class="table-container">
            <div class="table-header">
			<div class="d-flex justify-content-between align-items-center mb-2">
            <!-- Title on the left -->
            <h2>Last Orders (Web)</h2>
            
            <!-- Buttons on the right -->
            <div>
                <button class="btn btn-primary" type="button">View on map</button>
                <button class="btn btn-secondary" type="button">View All</button>
            </div>
        </div>

        <div class="d-flex justify-content-between align-items-center">
            <!-- Show entries on the left -->
            <div class="entries-dropdown">
                Show
                <select class="custom-select">
                    <option value="10" selected>10</option>
                    <option value="25">25</option>
                    <option value="50">50</option>
                    <option value="100">100</option>
                </select>
                entries
            </div>

            <!-- Search bar on the right -->
            <div class="search-bar">
                <input type="text" class="form-control" placeholder="Search" aria-label="Search">
                <button class="btn btn-outline-secondary" type="button">Search</button>
            </div>
        </div>
    </div>
    <table class="table">
      <thead>
        <tr>
          <th>Order Number</th>
          <th>Order Date</th>
          <th>Customer Name</th>
          <th>City</th>
          <th>Service Name</th>
          <th>Product</th>
          <th>Time</th>
          <th>Total Balance</th>
          <th>Status</th>
          <th>Action</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="order in orders" :key="order.id">          
          <td>{{ order.order_number }}</td>
          <td>{{ order.order_date }}</td>
          <td>{{ order.customer_name }}</td>
          <td>{{ order.city }}</td>
          <td>{{ order.service_name }}</td>
          <td>{{ order.product }}</td>
          <td>{{ order.time }}</td>
          <td>{{ order.total_balance }}</td>
          <td>{{ order.status }}</td>
          <td><button class="btn btn-primary">Details</button></td>
        </tr>
      </tbody>
    </table>


        </div>
    </div>
</template>

<script>
import axios from 'axios';
axios.defaults.baseURL = 'http://localhost:3000';

export default {
  name: 'ContentView',
  data() {
    return {
      orders: []
    };
  },
  created() {
    this.fetchOrders();
  },
  methods: {
    fetchOrders() {
      axios.get('/api/orders')
        .then(response => {
          this.orders = response.data;
        })
        .catch(error => {
          console.error('There was an error fetching the orders:', error);
        });
    }
  }
};
</script>

<style scoped>

.table-header {
	/* Ensures there is no margin collapse */
	overflow: auto;
}

.content {
    margin-left: 20px; /* Same as the sidebar width */    	
    margin-right: 20px; /* Same as the sidebar width */
    flex-grow: 1;
    padding-top: 20px; 
}

.table-container {
  width: 100%; /* Ensure the container takes the full width */
  padding: 20px; /* Add padding inside the container for spacing */
}

.search-bar {
    display: flex;
    align-items: center;
}

.search-bar .form-control {
    margin-right: 0.5rem; /* Spacing between input and button */
}

</style>
